'use strict';

angular.module('videoHub')
  .directive('listNavBar', function() {
    return {
      'restrict': 'E',
      'templateUrl': 'shared/listnavbar/listnavbar.html',
      'scope': {
        'activeNavbarTab': '@'
      },
      'controller': function($scope, $rootScope, $location, CurrentUser, LOGO_URL) {
        $scope.LOGO_URL = LOGO_URL;
        $scope.currentUserName = CurrentUser.getCurrentUser();
        $scope.logout = function() {
          CurrentUser.logout();
        };
      }
    };
  });
