'use strict';
angular.module('videoHub')
  .directive('listPageColumn', function(){
    return {
      templateUrl: 'shared/list-page-column/list-page-column.html',
      replace: true,
      restrict: 'A',
      scope: {
        'length': '@',
        'text': '@',
        'columnWidth': '@'
      },
      link: function(scope){
        if (scope.text.length > scope.length) {
          scope.truncatedText = _.trunc(scope.text, scope.length);
          scope.tooltipText  = scope.text;
          scope.displayText = scope.truncatedText;
          scope.tooltipTrigger = null;
        } else {
          scope.tooltipTrigger = 'hover';
          scope.displayText = scope.text;
        }
      }
    };
  });
