'use strict';

angular.module('videoHub')
  .controller('ChannelDetailController', function (
    $scope, $routeParams, ChannelService, $location, AlertService, DetailPageControllerConstants,
    Restangular, SaveButtonStateService) {

    $scope.channel = {};
    $scope.fields = {};
    $scope.accent_color = {};
    $scope.slides = [];

    var displaySomethingWentWrongAlert = function () {
      AlertService.error(DetailPageControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    };

    var validateAccentColorFields = function (input) {
      if (0 <= input <= 256) {
        return input;
      } else {
        return '';
      }
    };

    var normalizeSlideIndicies = function() {
      // update indicies when sortable is updated
      _.each($scope.slides, function(slide, i) {
        slide.position = i;
      });
    };

    $scope.getChannel = function () {
      if (_.has($routeParams, 'channelId')) {

        return ChannelService.one($routeParams.channelId).get().then(
          function (data) {
            $scope.channel = data;
            $scope.fields.partner = $scope.channel.partner;
            _.forEach(data.slides, function(slide) {
              $scope.slides.push(slide);
            });
            var accentColorStringAsArray = _.words(data.accent_color);
            if (accentColorStringAsArray.length >= 1) {
              $scope.accent_color.r = _.parseInt(accentColorStringAsArray[0]) || 0;
            }

            if (accentColorStringAsArray.length >= 2) {
              $scope.accent_color.g = _.parseInt(accentColorStringAsArray[1]) || 0;
            }

            if (accentColorStringAsArray.length >= 3) {
              $scope.accent_color.b = _.parseInt(accentColorStringAsArray[2]) || 0;
            }

          },
          function (data) {
            console.log(data);
          }
        );
      } else {
        $scope.channel = {};
        Restangular.restangularizeElement(null, $scope.channel, 'channels');
      }
    };

    $scope.save = function () {
      if ($scope.channelDetailForm.$valid) {
        if (_.has($scope.fields.partner, 'id')) {
          $scope.channel.partner = $scope.fields.partner.id;
        } else {
          $scope.channel.partner = null;
        }

        $scope.channel.accent_color = $scope.getRGBValueFromFields();

        $scope.channel.slides = $scope.slides;

        $scope.channel.save().then($scope.successCallback, displaySomethingWentWrongAlert);
      } else {
        if (_.has($scope.channelDetailForm.$error, 'number')) {
          AlertService.error('Accent Color values must be numbers.', true);
        } else {
          AlertService.error(DetailPageControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
        }
      }
    };

    $scope.getRGBValueFromFields = function () {
      var compiled = _.template('<%= r %>,<%= g %>,<%= b %>');
      var params = {
        'r': $scope.accent_color.r || 0,
        'g': $scope.accent_color.g || 0,
        'b': $scope.accent_color.b || 0
      };

      return compiled(params);
    };

    $scope.successCallback = function (channel) {
      $scope.channelDetailForm.$setPristine();
      SaveButtonStateService.displaySavingAnimation().then(function () {
        if (!_.has($routeParams, 'channelId')) {
          $location.path('cms/channel/' + channel.id + '/');
        }
      });
    };

    $scope.$watch('accent_color.r', function (newValue) {
      $scope.accent_color.r = validateAccentColorFields(newValue);
    });

    $scope.$watch('accent_color.g', function (newValue) {
      $scope.accent_color.g = validateAccentColorFields(newValue);
    });

    $scope.$watch('accent_color.b', function (newValue) {
      $scope.accent_color.b = validateAccentColorFields(newValue);
    });

    $scope.dateTimeSelectorCallback = function (newDate) {
      $scope.channel.published = newDate;
      $scope.save();
    };

    $scope.delete = function () {
      AlertService.info('If you would like to delete a channel, please contact your friendly sysadmin.', true);
    };

    $scope.getChannel();

    // ui-sortable setup
    $scope.sortableOptions = {
      containment: 'document',
      opacity: 0.75,
      placeholder: 'dropzone',
      stop: function() {
        $scope.channelDetailForm.$setDirty();
        normalizeSlideIndicies();
      }
    };

    /**
     * Add a new slide.
     */
    $scope.addSlide = function() {
      if (!_.isArray($scope.slides)) {
        // ensure slider slides list is initialized
        $scope.slides = [];
      }

      $scope.slides.push({});
      $scope.channelDetailForm.$setDirty();
      normalizeSlideIndicies();
    };

    /**
     * Remove a slide.
     *
     * @param {Object} slide - slide to remove from slider.
     */
    $scope.removeSlide = function(slide) {
      if (slide) {
        var i = _.findIndex($scope.slides, slide);
        if (i >= 0) {
          $scope.slides.splice(i, 1);
          $scope.channelDetailForm.$setDirty();
          normalizeSlideIndicies();
        }
      }
    };
  });
