'use strict';

angular.module('videoHub').directive('navDropdown', [
  function() {
    return {
      controller: function($scope) {
        $scope.sections = [{
          href: 'cms/',
          title: 'Videos'
        }, {
          href: 'cms/series/',
          title: 'Series'
        }, {
          href: 'cms/channels/',
          title: 'Channels'
        }, {
          href: 'cms/sponsors/',
          title: 'Sponsors'
        }];

        $scope.sections = _.sortBy($scope.sections, function(element){
          return element.title;
        });
      },
      restrict: 'E',
      scope: {
        active: '@'
      },
      templateUrl: 'shared/nav-dropdown/nav-dropdown.html'
    };
  }
]);
