'use strict';
angular.module('videoHub')
  .service('ListService', function($location, $injector, ValidQueryParamsForListPage) {

    this.data = {};
    var ListService = this;
    this.service = null;
    var currentService = null;

    this.getParams = function() {
      return this.data.params;
    };

    this.setService = function(serviceName) {
      this.service = $injector.get(serviceName);
      currentService = this.service;
      this.data.params = $location.search();
      this.data.list = [];
      this.data.totalItems = 0;
    };

    this.clearSearch = function() {
      this.data.params.q = '';
    };

    this.setFilter = function(newFilterValue) {
      this.data.params.f = newFilterValue;
    };

    this.setList = function(list) {
      ListService.data.list = list;
      if (list.meta) {
        ListService.data.totalItems = list.meta.count;
      } else {
        list.meta = {};
        list.meta.count = 0;
      }
    };

    this.setSearch = function(searchQuery) {
      this.data.params.q = searchQuery;
    };

    this.setOrder = function(newOrderValue) {
      var orderBy = this.data.params.order_by;
      var orderByContainsNewOrderValue = _.endsWith(orderBy, newOrderValue);
      var orderByIsAlreadyInverted = _.startsWith(orderBy, '-');
      if (orderByContainsNewOrderValue && !orderByIsAlreadyInverted) {
        orderBy = '-' + newOrderValue;
      } else if (orderByContainsNewOrderValue && orderByIsAlreadyInverted) {
        //If the existing orderBy value is already inverted, remove it.
        orderBy = null;
      } else {
        orderBy = newOrderValue;
      }

      this.data.params.order_by = orderBy;
    };

    this.setPage = function(pageNumber) {
      this.data.params.page = pageNumber;
    };

    this.updateList = function() {
      this.data.params = _.pick(this.data.params, ValidQueryParamsForListPage);
      this.data.params = _.omit(this.data.params, function(value) {
        return _.isEmpty(value) && !_.isNumber(value);
      });
      $location.search(this.data.params);
      return currentService.getList(this.data.params).then(ListService.setList);
    };

    this.clearQueryParams = function(){
      ListService.data.params = {};
      $location.search(this.data.params);
    };
  });
